.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas:
    "h h"
    "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  grid-area: c;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    4deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(29, 195, 253, 1) 50%,
    rgba(69, 252, 170, 0.5270483193277311) 100%
  );
}
